/* Section2 */
.section2 {
    background: #ffffff;
    padding: 48px 0;
  }
  
  .section2 .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 16px;
  }
  
  .section-title {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 32px;
  }
  
  .job-card {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  @media (min-width: 768px) {
    .job-card {
      gap: 48px;
    }
  }
  
  .job-dates {
    font-size: 16px;
    color: #666;
    display: flex;
    gap: 8px;
    margin-bottom: 16px;
  }
  
  .job-main {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  @media (min-width: 768px) {
    .job-main {
      flex-direction: row;
      align-items: flex-start;
    }
  }
  
  .company-logo {
    width: 60px;
    height: 60px;
    object-fit: contain;
    margin-right: 16px;
  }
  
  .job-info {
    flex: 1;
  }
  
  .job-role {
    font-size: 20px;
    margin: 0 0 8px;
  }
  
  .company-desc {
    font-size: 14px;
    color: #444;
    margin-bottom: 16px;
  }
  
  .job-tasks {
    list-style: disc inside;
    font-size: 14px;
    color: #333;
    line-height: 1.6;
  }
  
  .subprojects {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  @media (min-width: 768px) {
    .subprojects {
      flex-direction: row;
    }
  }
  
  .subproject {
    background: #f5f5f5;
    border-radius: 12px;
    padding: 16px;
    display: flex;
    align-items: center;
    gap: 16px;
    flex: 1;
  }
  
  .sub-left h4 {
    margin: 0 0 8px;
    font-size: 16px;
    font-weight: 600;
  }
  
  .sub-left p {
    margin: 0;
    font-size: 14px;
    color: #555;
  }
  
  .sub-img {
    width: 100px;
    border-radius: 8px;
    object-fit: cover;
  }
  